/* ============================================================================= HTML5 CSS Reset Minified - Eric Meyer ========================================================================== */
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; outline: 0; font-size: 100%; vertical-align: baseline; background: transparent; }

body { line-height: 1; }

mark { background-color: #ff9; color: #000; font-style: italic; font-weight: bold; }

input, select { vertical-align: middle; }

canvas { display: block; vertical-align: bottom; position: absolute; top: 0; }

#particles-js { height: 100%; background-image: linear-gradient(to right, #ffd12d 0px, #efedd3 51%, #d2e9ff 100%); background-size: cover; background-position: 50% 50%; background-repeat: no-repeat; }

.body { font-family: "TitilliumWeb", serif; }

a { cursor: pointer; }

.wrapper { width: 80%; margin: 0 auto; }

@media screen and (max-width: 1024px) { .wrapper { width: 90%; } }

.main-button { display: flex; justify-content: center; align-items: center; flex-direction: row; padding: 17px 24px; background-color: #ffd12d; border-radius: 36px; border: solid 2px #ffd12d; color: #282828; cursor: pointer; width: 229px; height: 55px; }

.main-button__text { font-family: "TitilliumWebSemiBold",serif; font-size: 14px; padding-right: 40px; }

.main-button:hover { background-color: #ffd84b; }

.main-button:focus { background-color: #ffd84b; border: 1px solid black; outline: none; }

.news { margin-bottom: 120px; }

.news-block { text-align: center; margin-bottom: 60px; }

.news-block__title { font-family: "TitilliumWebBold",serif; font-size: 40px; line-height: 1.3; }

.news-block__text { font-family: "TitilliumWebSemiBold",serif; font-size: 18px; line-height: 1.56; }

.news-slider__item { position: relative; display: flex !important; justify-content: space-between; align-items: stretch; flex-direction: column; height: 260px !important; margin: 0 15px; padding: 20px 20px 30px; border-radius: 18px; color: white; margin-bottom: 40px; }

.news-slider__item:focus { outline: none; }

.news-slider__block-categories { display: flex; justify-content: flex-end; align-items: center; flex-direction: row; flex-wrap: wrap; }

.news-slider__categories { padding: 6px 12px; margin-right: 8px; margin-bottom: 2px; border-radius: 6px; color: white; background-color: rgba(220, 220, 220, 0.5); font-family: "TitilliumWebSemiBold",serif; font-size: 12px; }

.news-slider__categories:hover { background-color: #ffd12d; color: black; }

.news-slider__text { font-family: "TitilliumWebSemiBold",serif; font-size: 16px; line-height: 1.5; }

.news-slider .slick-prev, .news-slider .slick-next { position: absolute; z-index: 1; bottom: -54px; width: 54px; height: 54px; border: solid 2px #dcdcdc; border-radius: 50%; background: transparent; background: url(../img/arrow-right-s-line.svg) no-repeat center; }

@media screen and (max-width: 480px) { .news-slider .slick-prev, .news-slider .slick-next { display: none !important; } }

.news-slider .slick-prev:hover, .news-slider .slick-next:hover { border: solid 2px #ffd12d; background: url(../img/arrow-right-s-line-hover.svg) no-repeat center; cursor: pointer; }

.news-slider .slick-prev { right: 67px; transform: rotate(180deg); }

.news-slider .slick-next { right: 0; }

@font-face { font-family: "TitilliumWebRegular"; src: url("../fonts/TitilliumWeb-Regular.ttf") format("truetype"); font-weight: 400; font-style: normal; }

@font-face { font-family: "TitilliumWebSemiBold"; src: url("../fonts/TitilliumWeb-SemiBold.ttf") format("truetype"); font-weight: 600; font-style: normal; }

@font-face { font-family: "TitilliumWebBold"; src: url("../fonts/TitilliumWeb-Bold.ttf") format("truetype"); font-weight: 700; font-style: normal; }


